.TimelineItem {
  padding: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12); }
  .TimelineItem__images {
    margin-top: 8px; }
  .TimelineItem__image {
    border-bottom: 1px solid rgba(0, 0, 0, 0.12); }
    .TimelineItem__image:first-of-type {
      border-top-left-radius: 4px;
      border-top-right-radius: 4px; }
    .TimelineItem__image:last-of-type {
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      border-bottom: none; }
  .TimelineItem__date {
    font-size: 16px;
    line-height: 30px;
    color: rgba(0, 0, 0, 0.54); }
  .TimelineItem__text p:last-child {
    margin: 0; }
  .TimelineItem:last-of-type {
    border-bottom: none; }



